.div1 {
  width: 411px;
  height: 339px;
}
.div1 .titleNode {
  width: 137px;
  height: 37px;
  /* float: left; */
  line-height: 37px;
  text-align: center;
  cursor: pointer;
  position: relative;
  /* margin-left: 88px; */
  font-size: 16px;
  text-shadow: 0 0 30px #f4faf5;
  color: #fff;
}
.div1 .realtime {
  width: 411px;
  height: 292px;
  border-image-repeat: stretch;
}
.div1 .realtime .demo {
  overflow: hidden;
  height: 380px;
}
.div1 .realtime .demo2 {
  margin-top: -20px;
}
.div1 .realtime .smalltable {
  width: 382px;
  height: 270px;
  overflow: hidden;
}
.div1 .realtime .smalltable .table {
  width: 100%;
  padding-top: 50px;
  padding-left: 60px;
  margin: 23px 0 0 18px;
}
.div1 .realtime .smalltable .table th {
  height: 29px;
  border-top: none;
  border-bottom: 0px solid red;
  font-weight: bold;
  line-height: 1;
  font-size: 14px;
  padding: 7px 6px;
  color: white;
  text-align: center;
}
.div1 .realtime .smalltable .table th:nth-child(1) {
  width: 104px;
}
.div1 .realtime .smalltable .table td:nth-child(2) {
  width: 103px;
}
.div1 .realtime .smalltable .table td {
  height: 33px ;
  border-top: none;
  border-bottom: 0px solid red;
  line-height: 1;
  font-size: 14px;
  padding: 7px 6px;
  color: white;
  text-align: center;
}
.div1 .realtime .smalltable .table .striped1 {
  background-color: #172362;
}
.div1 .realtime .smalltable .table .striped1 tr {
  text-align: center;
}
.div1 .realtime .smalltable .table .striped {
  background-color: #0a5bb2;
}
.div1 .realtime .smalltable .table .striped2 {
  background-color: #133884;
}
